<template>
  <div>

    <div class="pagebar-title">新增传感器设备模板</div>

    <div class="module">
      <div class="mtitle">模板设备名</div>
      <el-input v-model="name" placeholder="模板名"></el-input>
    </div>

    <div class="module">
      <el-button @click="push()">提交</el-button>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import { Categoryadd } from "@/api/paramtemplate";
export default {
  name: "Home",
  data() {
    return {
      name: null
    };
  },
  components: {
  },
  methods: {
    push() {
      this.$http(Categoryadd({
        name: this.name
      }), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            duration: 5 * 1000,
          });
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
  },
  created() {
  }
};
</script>
<style lang="scss" scoped>
</style>